import React from 'react';
import PropTypes from 'prop-types';
import './styles.scss';

const Section = ({title, children}) => (
  <div>
    <h2 className="section-title">{title}</h2>
    {children}
  </div>
);

Section.propTypes = {
  title: PropTypes.string,
  children: PropTypes.element,
}

export default Section;
